                    <p class="mt-5">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusamus non esse earum perspiciatis in error voluptatum, nisi minima praesentium laborum, asperiores voluptate culpa maiores ea itaque a possimus eos quidem.</p>
                    
                    <div class="text-left mt-5 pt-5">
                        <span style="font-size:24px;" > Swipe <i class="las la-arrow-right"></i> </span>
                    </div>
                    

                </div>
            </div>

        </div>

        <div class="col-12 col-md-6 pb-md-5">

            <div class="row">
                <div class="col-11 col-md-8 mx-auto">

                    <div class="mt-4 mb-3 pb-3">
                        <span class="text-grey">Have an account?</span> <router-link to="login"><a href="#"><span class="text-theme">Sign In</span></a></router-link>
                    </div>

                    <div class="auth-header mb-2 pb-2">
                        <span class="font-weight-bold h4">Welcome to</span>
                        <h1 class="font-weight-bold">GOOPHO</h1>
                    </div>

                    <form @submit.prevent="registerUser">
                        <div class="form-group mb-4">
                            <input v-model="userData.name" type="text" class="form-control customInput  " id="usernameInput" Placeholder="Name">
                            <small v-if="errors.name" class="text-danger">name issue</small>
                        </div>
                        <div class="form-group mb-4">
                            <input v-model="userData.username" type="text" class="form-control customInput  " id="nameInput" Placeholder="UserName">
                            <small v-if="errors.username" class="text-danger">username issue</small>
                        </div>
                        <div class="form-group mb-4">
                            <input v-model="userData.email" type="email" class="form-control customInput  " id="emailInput" aria-describedby="emailHelp" Placeholder="Email">
                            <small v-if="errors.email" class="text-danger">email issue</small>
                        </div>
                        <div class="form-group mb-4">
                            <input v-model="userData.password" type="password" class="form-control customInput  " id="passwordInput" Placeholder="Password">
                            <small v-if="errors.password" class="text-danger">word issue</small>
                        </div>
                        <div class="form-group mb-4">
                            <input v-model="userData.password_confirmation" type="password" class="form-control customInput  " id="passwordConfirmInput" Placeholder="Confirm Passoword">
                            <small v-if="errors.confirmpassword" class="text-danger">mathicng issue</small>
                        </div>

                        <div class="row mb-4">
                            <span class="col-11 mx-auto small">
                                By creating an account you're agreeing with our <a href="" class="text-dark font-weight-bold">Terms of Use </a>,
                                <a href="" class="text-dark font-weight-bold">Privacy Policy</a>, and <a href="" class="text-dark font-weight-bold">Email Notifications</a>.
                            </span>
                        </div>

                        <div class="text-danger text-center mt-2 mb-4 p-3 border error" v-if="errors">
                            {{errors}}
                        </div>
                        
                        <button :disabled="registering" type="submit" class="btn customInputButton mb-4">
                            <div v-show="!registering">Create account</div>
                            <span v-show="registering" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                        </button>

                        <hr class="divider mb-5">

