</template>

<script>

    export default {
        name: 'register',
        data(){
            return{
                userData:{
                    name:'',
                    username:'',
                    email:'',
                    password:'',
                    password_confirmation:'',
                },
                errors:'',
                registering:false,
                //use:'',
            }  
        },      
        methods:{
            registerUser:function(){
                this.registering = true
                
                this.errors = '';
                if(this.userData.password !== this.userData.password_confirmation){
                    this.errors = 'Passwords do not match'
                    this.registering = false
                }
                else{

                    this.errors = '';
                    this.$http.post('http://127.0.0.1:5000/signup',this.userData)
                    .then((res) =>{
                        this.errors = '';
                        this.$router.push("/")
                        this.registering = false
                        this.$store.commit("loggedInSaveAuth",res.data)
                        /*console.log(res.data)
                        console.log(this.userData)*/
                    })
                    .catch((err) => {
                        this.registering = false
                        //console.log(err.response)
                        this.errors = err.response.data.message
                    })

                }
            },
        },
    }
</script>
    
